@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500&family=Quicksand:wght@700&display=swap);
/* GENERAL */

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #39393e;
  display: flex;
  flex-direction: column;
}

/* NAVEGACION */

nav {
  padding: 3vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 10vh;
}

nav img {
  max-height: 30vh;
}

/* BOTONES */

.cuelga_oferta {
  background-color: #5dd39a;
  border: none;
  font-size: 1.3em;
  font-weight: 600;
  padding: 10px;
  border-radius: 20px;
  color: #fff;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.249);
}
.cuelga_oferta:hover {
  background-color: #277a52;
}

.show_more {
  align-items: center;
  justify-self: end;
}
.show_more button {
  background-color: #397cc1;
  border: none;
  padding: 10px;
  border-radius: 10px;
  text-transform: uppercase;
  color: #fff;
  font-weight: 600;
  font-size: 15px;
}
.show_more button:hover {
  background-color: #2a5783;
}

/* CONTAINERS */

.container_general {
  max-width: 80%;
  margin: auto;
  flex-grow: 1;
  padding: 30px;
}

.container_oferta {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.249);
  border-radius: 20px;
  padding: 20px;
  margin-bottom: 20px;
  width: 100%;
  list-style: none;
}

/* FOOTER */

footer {
  display: flex;
  padding: 50px;
  align-items: center;
  justify-content: space-between;
  background-color: #617488;
  color: #fff;
  font-size: 12px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

.autores ul {
  display: grid;
  grid-template: 1fr 1fr / 1fr 1fr;
  grid-gap: 0px 20px;
  list-style: none;
}

/* QUERIES */

@media (max-width: 800px) {
  #container_oferta {
    display: flex;
    flex-direction: column;
  }
}

.oferta_experiencia h3 {
  font-weight: 300;
}

/* CONTAINER */

.container_general_oferta {
  height: 80vh;
  width: 70vw;
  margin: auto;
}

.container_oferta_ind {
  margin: 20px;
  background-color: #f9fafc;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.281);
  border-radius: 20px;
  padding: 50px;
}

.cuerpo_oferta {
  display: flex;
  margin: 15px;
}
.columna_izq_oferta {
  width: 30%;
  list-style: none;
}
.columna_der_oferta {
  width: 70%;
}

/* BUTTONS */

.back_btn,
a {
  border: none;
  background-color: transparent;
  display: flex;
  align-items: center;
  font-size: 15px;
  color: #617488;
  padding: 20px;
  text-decoration: none;
}
.back_btn svg {
  width: 30px;
}

.contact_btn {
  background-color: 0.397cc1;
  padding: 10px;
  border: none;
  border-radius: 10px;
  color: #fff;
  font-size: 15px;
  font-weight: bold;
  background-color: #397cc1;
}
.contact_btn:hover {
  background-color: #235281;
}

